<template lang="html">
  <v-app>
    <v-container class="mainContainer margtop">
      <v-layout row class="text-xs-center pic mb-5" v-for="(item,i) in pics.slike" :key="i">
        <v-flex xs12>

          <clazy-load  :src="getPic(item)" class="pictrans">
          <!-- The image slot renders after the image loads. -->

            <img :src="getPic(item)" slot="image" >

          <!-- The placeholder slot displays while the image is loading. -->

          <v-flex class="mt-5 text-xs-center" slot="placeholder">
            <v-progress-circular indeterminate color="gray" :width="4" :size="65"></v-progress-circular>
          </v-flex>

          </clazy-load>
          <v-flex v-text="pics.text" class="pictrans"></v-flex>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>

<script>
export default {
  props: ['id'],
  computed: {
    pics() {
      return this.$store.getters.LoadedAlbum(this.id)
    }
  },
    methods: {
      getPic(pic) {
        return require('@/assets/img/' + pic)
      }
    }
  }
</script>

<style scoped lang="css">
  .pictrans {
  animation: 1s ease-out fadein;
  transition: 1s;
}
  @keyframes fadein {
  0% {
    opacity: 0;
    transform: translate(100%);
  }
  70% {
    opacity: 0;
    transform: translate(-4%,10%);
    filter: blur(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
    filter: blur(0);
  }
}
</style>



